import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Classificacao_helpdesk } from '../../../modelo/Classificacao_helpdesk'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'classificacao_helpdesk-dados-edit',   templateUrl: 'classificacao_helpdesk_dadosEdit.component.html' }) export class Classificacao_helpdeskDadosEditComponent implements OnInit {    classificacao_helpdesk: Classificacao_helpdesk;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.classificacao_helpdesk.Classificacao_helpdesk";                 let id = +params['id'];         this.classificacao_helpdesk = new Classificacao_helpdesk();         this.classificacao_helpdesk.cd_classificacao_helpdesk = id;         o.objetoJson = this.classificacao_helpdesk;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a classificacao_helpdesk. ';       }      this.classificacao_helpdesk = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


